.top_blurbs,.bottom_blurbs{
	max-width: $wrapper-width;
  margin-left: auto;
  margin-right: auto;
	.blurbs{
		.blurb{
			display: flex;
			margin: 100px 0;
			justify-content: space-between;
    	align-items: center;
    	@include tablet{
    		display: block;
    		text-align: center;
    	}
			// .img_wrapper,.blub_content{
			// 	@include inline_middle();
			// }
			.img_wrapper{
				// width: calc(100%*2/5);
				
				margin-right: 40px;
				width: 340px;
				height: auto;
				height: 416px;
				object-fit: contain;
				// height: 175px;
				@include tablet{
					margin: 0 auto 40px;
					width: calc(340px*2/3);
					height: calc(416px*2/3);
				}
				&.to_show_responsive{
					display: none;
					@include tablet{
						display: block;
					}
				}
				&.to_hide_responsive{
					display: block;
					@include tablet{
						display: none;
					}
				}
			}
			.blub_content{
				// width: calc(100%*3/5);
				max-width: 610px;
				@include tablet{
					margin-left: auto;
					margin-right: auto;
				}
				// weird bug with empty h1
				h1:not(:first-child){
					margin-bottom: 30px;
				}
				strong{
					color: $accent_color;
					font-weight: 600;
				}
				p{
					line-height: 2.12;
				}
			}
		}
	}
}

.ce_que_nous_reparons{
	max-width:$wrapper-width;
	margin: auto;
	text-align:center;
	.ce_que_nous_reparons_wrapper{
		max-width: 560px;
		margin: auto;
		p{
			text-align: left;
			line-height: 2.12;
			margin: 35px auto 95px;
		}
	}
}

.bottom_blurbs{
	.title{
		text-align: center;
		color: $accent_color;
		border: solid 1px rgba(3, 19, 45, 0.1);
		padding: 20px 0 21px;
		margin-bottom: 10px;
		margin-top: 42px;
		h2{
			font-size: 27px;
			font-weight: 600;
			letter-spacing: -0.1px;
			text-align: center;
		}
	}
	.blurbs{
		display: flex;
		flex-wrap: wrap;
		@include tablet{
			display: block;
		}
		.blurb{
			width: 50%;
			justify-content: center;
			margin:42px 0;

			@include tablet{
				width: 100%;
			}

			.img_wrapper{
				width: 113px;
				height: 113px;
				margin-right: 30px;
				@include tablet{
					margin-left: auto;
					margin-right: auto;
				}
			}
			.blub_content{
				max-width: 330px;
				@include tablet{
					margin-left: auto;
					margin-right: auto;
				}
				h3{
					font-size: 22px;
					font-weight: 600;
					line-height: 1.55;
					margin-bottom: 30px;
				}
			}
		}
	}
}

