body{
	h1,h2,h3,h4,h5,h6,p{
		margin: 0;
	}
	h1, h1 span{
		font-size: $h1;
		font-weight: $heading_weight;
	}
	h2, h2 span{
		font-size: $h2;
		font-weight: $heading_weight;
	}
	p,span{
		font-weight: $body_weight;
		font-size: $body;
	}
	a{
		&:hover{
			cursor: pointer;
		}
		&.button{
			background-color: #fe9426;
			border-radius: 3px;
			padding: 12px 18px;
			color: #ffffff;
			text-align: center;
		}
		&.button_2{
			padding: 10px 35px 10px 20px;
			border:1px solid $blue_grey;
			border-radius: 3px;
			transition: 0.2s all ease-in-out;
			position: relative;
			span{
				font-weight: 500;
			}
			svg{
				margin-left: 15px;
				transition: 0.2s all ease-in-out;
				@include absolute_center_vertically();
			}
			&:hover{
				padding-left:15px;
				padding-right:40px;
				svg{
					margin-left: 25px;
				}
			}
		}
	}
	// section{
	// 	@include tablet{
	// 		margin:60px auto;
	// 	}
	// }

}