// Header container
.header {
	display:flex;
	justify-content: space-between;
	// width:80%;
	margin-left:auto;
	margin-right:auto;
	padding: 0 35px;
	// max-width:$wrapper-width;

	@include tablet{
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	&:before,&:after{
		display: none;
	}
	.logo, .navs_wrapper {
		// @include inline-middle();
		padding:20px 0;
	}

	.logo{
		position: absolute;
		@include tablet{
			position: relative;
		}
	}

	.navs_wrapper{
		margin-left:auto;
		@include tablet{
			margin-right: auto;
			display: flex;
    	align-items: center;
			// margin-right: auto;
			nav{
				flex-direction: column;
				justify-content: center;
				align-items: center;
				position: fixed;
				z-index: 100;
				width: 100vw;
				left: 100%;
				top: 0%;
				// opacity: 0;
				height: 100vh;
				background-color:rgba(255,255,255,0.95);
				transition: left 0.2s ease-in-out;
				&.tablet_menu_active{
					left: 0;
				}
			}
			
		}
	}

	.logo, .logo a, svg {
		@include tablet{
			max-width: 220px;
			text-align: center;
		}
		@include phone{
			max-width: 180px;
			align-self: left;
			padding-bottom:0;
		}
	}


