// $height-icon: 25px;
// $width-line: 50px;
// $height-line: 4px;

$height-icon: 16px;
$width-line: 33px;
$height-line: 3px; 

$transition-time: 0.2s;
$rotation: 45deg;
$translateY: ($height-icon / 2);
$translateX: 0;

// @include phone{
// 	$height-icon: 16.6667px;
// 	$width-line: 33.3333px;
// 	$height-line: 6px;
// }

#hamburger-icon{
	//thanks @stevenfabred
	width:$width-line;
  height:$height-icon;
  z-index: 101;
  // position:relative;
  // vertical-align: middle;
  // margin-left: 20px;
  display: none;
  @include tablet{
  	display:inline-block;
  	position: absolute;
    top: 45px;
    right: 45px;
  }

  // margin: ($height-icon * 2) auto $height-icon auto;
  
  .line {
    display:block;
    background:$base_color;
    width:$width-line;
    height:$height-line;
    position:absolute;
    left:0;
    border-radius:($height-line / 2);
    transition: all $transition-time;
    -webkit-transition: all $transition-time;
    -moz-transition: all $transition-time;
    
    &.line-1 {
      top:0;
    }
    &.line-2 {
      top:50%;
    }
    &.line-3 {
      top:100%;
    }
  }
  &:hover, &:focus {
     .line-1 {
        transform: translateY($height-line / 2 * -1);
        -webkit-transform: translateY($height-line / 2 * -1);
        -moz-transform: translateY($height-line / 2 * -1);
     }
    .line-3 {
        transform: translateY($height-line / 2);
        -webkit-transform: translateY($height-line / 2);
        -moz-transform: translateY($height-line / 2);
    }  
  }
  &.active {
    .line-1 {
      transform: translateY($translateY) translateX($translateX) rotate($rotation);
      -webkit-transform: translateY($translateY) translateX($translateX) rotate($rotation);
      -moz-transform: translateY($translateY) translateX($translateX) rotate($rotation);
    }
    .line-2 {
      opacity:0;
    }
    .line-3 {
      transform: translateY($translateY * -1) translateX($translateX) rotate($rotation * -1);
      -webkit-transform: translateY($translateY * -1) translateX($translateX) rotate($rotation * -1);
      -moz-transform: translateY($translateY * -1) translateX($translateX) rotate($rotation * -1);
    }
  }
}

.tel_wrapper{
	text-align: right;
	margin-bottom:30px;
	@include tablet{
		text-align: center;
		margin-bottom:10px;
	}
	svg, span{
		@include inline-middle();
	}
	svg{
		border: 1px solid $blue_grey;
		border-radius:100%;
		padding: 5px 6px 7px;
		box-sizing:content-box;
	}
	span.phone_number{
		margin-left: 25px;
		// margin-right:25px;
		@include font-size($body);
		@include tablet{
			margin-left: 5px;
		}
		b{
			padding-right: 20px;
			border-right: solid 1px rgba(3, 19, 45, 0.1);
			// margin-right:25px;
			margin-right:7px;
			@include tablet{
				border:none;
				margin-right: 0;
				padding-right: 10px;
			}
		}
	}
}

.navs_wrapper{
	nav a{
		font-size: 16px;
	}
	.wpml-ls-legacy-dropdown{
		display: inline-block;
    width: 75px;
    vertical-align: middle;
    .wpml-ls-sub-menu{
		  border:none;
		}
		.wpml-ls-item{
			padding: 0;
	  	display: inline-block;
	    width: 75px;
	    a {
			  width: auto;
			  padding: 10px 27px 10px 10px;
			  display: inline-block;
			  font-family: $heading_font;

			  transition: all 0.2s ease-in-out;
			  border: solid 1px #e2e2e2;
			  // border-bottom-right-radius: 3px;
			  // border-bottom-left-radius: 3px;
			  border-radius: 3px;
			  span{
			  	font-weight: 600;
			    transition: all 0.2s ease-in-out;
			  }
			  &:after{
			  	color: $accent_color;
			  	font-size: 14px;
			  }
			  &:hover{
			    border-color:$accent_color;
			    background-color:#ffffff;
			    color: $accent_color;
			    span{
			        color: $accent_color;
			    }
			  }
			}
	    .sub-menu {
			  border: solid 1px #e2e2e2;
			  border-radius: 3px;
			  box-shadow: none;
			  width: auto;
			  border-top-right-radius: 0px;
			  border-top-left-radius: 0px;
			  padding: 0;
			  background: #ffffff;
			  .menu-item {
				  border: none;
				  margin-right: 0;
				  span {
					  color: #293147;
					  font-weight: 500;
					}
				}
			}
		}
		.wpml-ls-item{
			a.js-wpml-ls-item-toggle.wpml-ls-item-toggle{
				padding: 10px 30px 10px 10px;
			}
		}
	}
	
}