/**
 * partials/nav
 * ---
 * Menus
 */


.nav {
	@include tablet{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.menu {
		display: inline-block;
		ul{
			display: flex;
			padding-left:0;
			@include tablet{
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}
			li.page_item {
				color:red;
				display: inline-block;
				@include min-tablet{
					margin-right: 40px;
				}
				@include tablet{
					margin-bottom: 20px;
				}
				a{
					font-size: $body;
					padding-bottom: 7px;
				}
				&.current_page_item{
					a{
						border-bottom: solid 2px #9b7e2f;
					}
				}
			}
		}
		
	}
}






