.home_header{
	display:flex;
	align-items: center;
  text-align: center;

  @include tablet{
			display: block; 
		}
  
	.card_wrapper{
		width: 25%; 
		height: 560px;
		padding: 5px;

		@include tablet{
			width: 100%;
			display: block;
			height: 350px;
		}

		&.no_rotate .card, &.rotate .card .front{
			@include stan{
				background-size: contain;
			}
			@include tablet{
				background-size: cover;
			}
		}

		&.no_rotate, &.rotate .front, &.rotate .back{
			display: flex;
	    flex-direction: column;
	    justify-content: center;
		}

		&.rotate{
		  perspective: 1000px;

			&:hover,&.hover{
				.card{
					transform: rotateY(180deg);
				}
			}

			.card{
				transition: 0.6s;
				transform-style: preserve-3d;
				position: relative;
				width: 100%;
				height: 100%;

				// h1{
				// 	font-weight: 600;
				// }

				p{
					line-height: 2.12;
				}

				.front,.back{
					backface-visibility: hidden;
					@include absolute-fill();
					// padding: 20%;
					padding:15%;
				}

				.front{
					background-color:#ffffff;
					color: #ffffff;
					// padding: 10%;
					a{
						@include font-size(24px);
						font-weight: 600;
						line-height: 1.75;
					}
				}
				.back{
					transform: rotateY(180deg);
					background-color:#ffffff;
					text-align: left;
					// padding: 20%;
					h1{
						@include font-size(32px);
					}
					p{
						margin: 10px auto 50px;
					}
				}
			}
			
		}
		&.no_rotate{
			padding: 5px;
			// box-sizing:
			background-color:#ffffff;
			position: relative;
			
			.card{
				height: 100%;
				// @include absolute-fill();
				a{
					color: #ffffff;
					@include font-size(24px);
					font-weight: 600;
					line-height: 1.75;
					display: block;
					margin: 10px 0;
				}
			}
			
		}
	}
}

.home_sub_header{
	margin:60px 0 140px;
	display: flex;
  justify-content: space-around;
  align-items: center;

  @include macbook{
  	display: block;
  	text-align: center;
		margin:80px auto;
  	.tel_wrapper{
  		text-align: center;
  		margin-top: 20px;
  		width: 100%;
  	}
  }

	.devis_cta{
		@include inline-middle();
		@include macbook{
			width: 100%;
			text-align: center;
		}
	}
}

.dl_app_wrapper{
	padding: 15px 20px 20px 25px;
	border-radius: 3px;
	border: solid 1px $blue_grey;
	// width:calc(100%*2/3);
	margin-right:100px;
	background-color: #ffffff;
	@include inline-middle();
	@include macbook{
		margin-right: auto;
		margin-left: auto;
	}
	.dl_app_text_wrapper,.qr_wrapper{
		@include inline-middle();
	}
	.dl_app_text_wrapper{
		.dl_app_title, .dl_app_description{
			text-align: left;
		}
		.dl_app_title{
			@include font-size(18px);
			font-weight: $heading_weight;
		}
		.dl_app_description{
			font-size: 14px;
			font-weight: 600;
			color: $accent_color;
		}
	}
	.qr_wrapper{
		@include phone{
			margin-top: 20px;
		}
		.qr-couple{
			@include inline-middle();
			img{
				width: 64px;
				height: 64px;
			}
			svg{
				@include inline-middle();
				margin-right: 10px;
			}
			&.ios{
				margin-left: 100px;
				margin-right: 15px;
				border-right: 1px solid $blue_grey;
				padding-right: 20px;
				@include macbook{
					margin-left: 0px;
					// padding-right: 0px;
					border:none;
				}
				@include phone{
					margin: 0;
				}
			}
			// &.android{
			// 	@include phone{
			// 		padding-left: 10px;
			// 	}
			// }
		}
	}
}
.devis_cta{
	// width:calc(100%*1/3);
	text-align:right;
	.tel_wrapper{
		// margin-top:30px;
		margin-bottom:30px;
	}
}
.big_cta_section{
	margin:140px 0;
	background-color:black;
	height:425px;
	display:flex;
	@include tablet{
		display: block;
		margin:80px auto;
	}
	.cta_wrapper{
		margin: 55px 140px 180px 140px;
		@include tablet{
			margin: 0;
		}
		padding: 42px 24px;
		border: 1px solid $blue_grey;
		border-radius:3px;
		color: #ffffff;
		width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include tablet{
    	display: block;
    	text-align: center;
    	border:none;
    }
    .text_wrapper{
    	max-width: 350px; 
    	@include tablet{
    		display: block;
    		max-width: 100%;
    		width: 100%;
    		margin-bottom: 60px;
    	}
    	h1{
    		@include font-size(48px);
				// font-weight: bold;
				// line-height: 0.73;
				line-height: 1.2;
				margin-bottom: 20px;
    	}
    	p{
    		line-height: 1.88;
				letter-spacing: 0.2px;
    	}

    }
	}
}

.mobile_app_section{
	margin:220px auto 260px;
	display:flex;
	// justify-content:flex-end;
	position:relative;
	@include tablet{
		margin:80px auto;
		display: block;
	}

	.app_image_wrapper{
		position:absolute;
		width: calc(100%*2/5);
		text-align: center;
		top: 50%;
    transform: translateY(-50%);
    @include ipad{
			display: block;
			position: relative;
			width: 100%;
			top: 0;
			transform: none;
			text-align: center;
		}
		img{
			max-width: 260px;
		} 
	}
	.text_wrapper{
		// display:flex;
		// width: calc(100%*3/5);
		width: 100%;
		text-align: right;
		@include ipad{
			display: block;
			text-align: center;
		}
		.title{
			width: calc(100%*4/7);
			display: inline-block;
			text-align: left;
			@include ipad{
				width: 100%;
				display: block;
				text-align: center;
				margin-top: 40px;
			}
		}

		h2{
			font-weight: 400;
			max-width: 560px;
			@include ipad{
				max-width: 100%;
			}
			span, strong{
				color: $accent_color;
				font-weight: $heading_weight;
			}
			margin-bottom: 40px;
		}

		.details_wrapper{
			padding-top:40px;
			padding-bottom:60px;
			background-image: linear-gradient(to bottom, #f9fbfd, #f0f4f7);
			line-height: 1.69;
			.details{
				width: calc(100%*4/7);
				margin-left: auto;
				text-align: left;
				@include ipad{
					width: 100%;
					display: block;
					text-align: center;
				}
				p,strong{
					max-width: 600px;
					display: block;
					@include ipad{
						width: 100%;
						text-align: center;
						padding: 0 20px;
					}
				}
				p{
					margin-bottom: 15px;

				}
				.dl_app_wrapper{
					margin-top: 40px;
					border:none;
				}
			}

			
			// strong{
			// 	display: inline-block;
			// }
		}
	}
}

.mosaic_menu{
	display:flex;
	@include ipad{
		display: block;
	}
	.left_wrapper,.right_wrapper{
		width: calc(100%/4);
		@include ipad{
			width: 100%;
		}
	}
	.left_wrapper{
		.link_group{
			height: 250px;
			&:first-child{
				margin-bottom: 10px;
			}
			@include ipad{
				width: 100%;
				padding: 0;
				height: 500px;
			}
			@include phone{
				height: 250px;
			}
		}
	}
	.right_wrapper,.middle_wrapper{
		@include ipad{
			width: 100%;
			padding: 0;
		}
		.link_group{
			height: 510px;
			@include phone{
				height: 250px;
			}
		}
	}
	.middle_wrapper{
		width: calc(100%/2);
		padding: 0 10px;
		@include ipad{
			width: 100%;
			padding: 0;
			margin-bottom: 10px;
		}
	}
	.link_group{
		position:relative;
		.bottom_bar{
			position:absolute;
			bottom:0;
			color:#ffffff;
			display: flex;
			width: 100%;
			justify-content: space-between;
			align-items:center;
			padding:30px;
			background:linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%);
			p{
				font-weight: 500;
				@include font-size(20px);
			}
		}
	}
}

.wondercar_section{
