/**
 * partials/footer
 * ---
 */


.footer {
	background-color: #03132d;
	color:#ffffff;
	.footer_wrapper{
		padding: 90px 0 85px;
		display:flex;
		max-width:$wrapper-width;
		margin:auto;

		@include tablet{
			flex-direction: column;
			justify-content: center;
			align-items: center;
		}

		.devis, .footer_menu, .logo{
			width: 33%;
			@include tablet{
				width: 100%;
			}
		}
		.logo{
			height: 57px;
			@include tablet{
				margin: 60px auto;
			}
		}
		.devis{
			text-align: right;
			@include tablet{
				text-align: center;
			}
			svg{
				width: 13px;
				height: 13px;
				margin-right: 11px;
				path{
					fill: #fe9426;
				}
			}
			a{
				// margin-bottom: 40px;
			}
			.call{
				margin: 40px auto 15px;
			}
			p{
				font-size: 15px;
			}
			b{
				font-size: 17px;
				font-weight: 600;
			}
		}
	}
}
