.top_blurbs_icon_left, .bottom_blurbs_icon_left{
	max-width: $wrapper-width;
  margin-left: auto;
  margin-right: auto;
	.blurbs{
		.blurb{
			display: flex;
			margin: 100px 0;
			justify-content: space-between;
    	align-items: center;
    	@include tablet{
    		display: block;
    		text-align: center;
    	}
			// .img_wrapper,.blub_content{
			// 	@include inline_middle();
			// }
			.img_wrapper{
				// width: calc(100%*2/5);
				
				margin-right: 40px;
				width: 175px;
				height: 175px;
				@include tablet{
					margin: 0 auto 40px;
				}
			}
			.blub_content{
				// width: calc(100%*3/5);
				max-width: 740px;
				h1{
					margin-bottom: 30px;
				}
				p{
					line-height: 2.12;
				}
			}
		}
	}
}

.services_sub_header{
	margin:60px 0 140px;
	padding: 65px 0;
	border-top: solid 1px rgba(3, 19, 45, 0.1);
	border-bottom: solid 1px rgba(3, 19, 45, 0.1);
	display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: $wrapper-width;
  margin-left: auto;
  margin-right: auto;

  @include tablet{
  	display: block;
  	text-align: center;
  	@include tablet{
			margin:80px auto;
		}
  	.tel_wrapper{
  		text-align: center;
  		margin-top: 20px;
  	}
  }

	.devis_cta{
		@include inline-middle();
	}
}