/**
 * modules/variables
 * ---
 */


/**
 * Typography
 */

$h1 : 36px;
$h2 : 30px;
$h3 : 26px;
// $h4 : ($h3)*3/4;
// $h5 : ($h4)*3/4;
$body : 16px;

$heading_font : 'Open Sans', serif; 
$body_font : 'Open Sans', sans-serif; 

$heading_weight: 600;
$body_weight: 300;

$font-family-base: $body_font, Helvetica, Arial, "Open Sans", sans-serif;
$font-size-base: 140%;
$line-height-base: 1.5;

/**
 * Colors
 */



$base_color: #040f22;
$accent_color: #896c24;
$blue_grey:#e2e2e2;

$text-color: $base_color;
$body-bg: #fff;
$selection-bg: $accent_color;

/**
 * Grid
 */

// $wrapper-width: 1280px;
$wrapper-width: 1080px;


$padding-horizontal-base: 5px;
$padding-vertical-base: 5px;
$margin-horizontal-base: 5px;
$margin-vertical-base: 5px;
$margin-horizontal-large: 30px;
$margin-vertical-large: 3px;